import { forwardRef } from "react";
import { FaCalendarAlt, FaMapMarkerAlt, FaRegClock } from "react-icons/fa";
import { Element } from "react-scroll";

import Col from "../../components/Col";
import Container from "../../components/Container";
import Maps from "../../components/Maps";
import Paper from "../../components/Paper";
import Row from "../../components/Row";

type Props = {
  children?: React.ReactNode;
};

const Schedule = forwardRef(
  ({ children }: Props, ref: React.Ref<HTMLDivElement>) => {
    const akadLocation = {
      lat: -8.566401027036859,
      lng: 118.98280382900735,
      link: "https://goo.gl/maps/zWaJdd7zDm14gKsL8",
    };

    const receptionLocation = {
      lat: -8.575632000257988,
      lng: 119.00023899454625,
      link: "https://goo.gl/maps/zpnJWYJXG4PM3iyR6",
    };

    return (
      <Element name="Schedule">
        <Container fullWidth>
          <Row align="center">
            <Col size={12}>
              <h3 className="text-center">
                Save the <strong>date!</strong>
              </h3>
            </Col>
          </Row>
          <Row align="center" verticalAlign="top" noMargin>
            <Col size={4} mobile={10}>
              <Paper background="primary">
                <Row align="center" noMargin>
                  <Col size={12}>
                    <div>
                      <h2 className="text-center">Akad</h2>
                      <p className="text-center">
                        Acara <strong>akad pernikahan</strong> kami insya Allah
                        akan dilaksanakan pada:
                      </p>
                      <p className="text-left">
                        <FaCalendarAlt></FaCalendarAlt> <strong>Sabtu</strong>,
                        8 Oktober 2022
                      </p>
                      <p className="text-left">
                        <FaRegClock></FaRegClock> 09.00 <strong>WITA</strong> -
                        Selesai
                      </p>
                      <p className="text-left">
                        <FaMapMarkerAlt></FaMapMarkerAlt>{" "}
                        <span>
                          <strong>Masjid Al Munawarah</strong> Kecamatan Sape,
                          Kabupaten Bima, Nusa Tenggara Barat
                        </span>
                      </p>
                    </div>
                  </Col>
                  <Col size={12}>
                    <Maps {...akadLocation}></Maps>
                  </Col>
                </Row>
              </Paper>
            </Col>
            <Col size={4} mobile={10}>
              <Paper>
                <Row align="center" verticalAlign="verticalCenter" noMargin>
                  <Col size={12}>
                    <div>
                      <h2 className="text-center">Resepsi</h2>
                      <p className="text-center">
                        Acara <strong>resepsi pernikahan</strong> kami insya
                        Allah akan dilaksanakan pada:
                      </p>
                      <p className="text-left">
                        <FaCalendarAlt></FaCalendarAlt> <strong>Sabtu</strong>,
                        8 Oktober 2022
                      </p>
                      <p className="text-left">
                        <FaRegClock></FaRegClock> 14.00 <strong>WITA</strong> -
                        Selesai
                      </p>
                      <p className="text-left">
                        <FaMapMarkerAlt></FaMapMarkerAlt>{" "}
                        <strong>Gedung Serba Guna</strong> Kecamatan Sape,
                        Kabupaten Bima, Nusa Tenggara Barat
                      </p>
                    </div>
                  </Col>
                  <Col size={12}>
                    <Maps {...receptionLocation}></Maps>
                  </Col>
                </Row>
              </Paper>
            </Col>
          </Row>
        </Container>
      </Element>
    );
  }
);

export default Schedule;
