import React from "react";
import GoogleMapReact from "google-map-react";

import { FaMapPin, FaMapMarkedAlt } from "react-icons/fa";

import styles from "./style.module.css";
import Button from "../Button";

type Props = { lat: number; lng: number; link?: string };

type MarkerProps = {
  lat: number;
  lng: number;
};

const Marker: React.FC<MarkerProps> = () => (
  <div style={{ position: "absolute", transform: "translate(-50%, -50%)" }}>
    <FaMapPin size={60} color="red"></FaMapPin>
  </div>
);

const Maps: React.FC<Props> = ({ lat, lng, link }) => {
  const defaultProps = {
    center: {
      lat,
      lng,
    },
    zoom: 18,
  };

  const createOptions = (maps: GoogleMapReact.Maps) => {
    return {
      disableDefaultUI: true,
    };
  };

  return (
    <div className={styles.map}>
      <GoogleMapReact
        bootstrapURLKeys={{ key: "AIzaSyB2mRLW5Xf1MhNzBVkfdnpKK5Zb5AvG-oA" }}
        center={defaultProps.center}
        zoom={defaultProps.zoom}
        options={createOptions}
      >
        <Marker lat={lat} lng={lng} />
      </GoogleMapReact>
      {link && (
        <div className={styles.button}>
          <a href={link}>
            <Button onClick={() => {}} color="dark" size="s" variant="fill">
              <FaMapMarkedAlt size={20} color="white"></FaMapMarkedAlt>{" "}
              <span className={styles.textIcon}>Buka di Maps</span>
            </Button>
          </a>
        </div>
      )}
    </div>
  );
};

export default Maps;
